@use 'base/variables' as v;
@use "sass:color";

.alerta {
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 1rem 0;
    font-size: 1.3rem;
    max-width: 80rem;

    &.error {
        background-color: color.adjust(v.$rojo, $lightness: 60%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }
    &.exito {
        background-color: color.adjust(v.$verde, $lightness: 60%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }
}