@use 'base/variables' as v;
@use 'base/mixins' as m;

.crear {
    margin-top: 5rem;
    .uptask {
            @include m.textDegradado(v.$morado);
        }
    }
.crear-form{
    input[type="submit"] {
        border-radius: 1rem;
        @include m.boton(v.$morado, v.$blanco);
    }
    .acciones {
        a {
            color: v.$morado;
        }
    }
}
