@use 'base/variables' as v;
@use 'base/mixins' as m;

.reestablecer {
    margin-top: 5rem;
    .uptask {
            @include m.textDegradado(v.$azul);
        }
    }
.reestablecer-form {
    input[type="submit"] {
        border-radius: 1rem;
        @include m.boton(v.$azul, v.$blanco);
    }
    .acciones {
        a {
            color: v.$azul;
        }
    }
}
