@use 'base/variables' as v;
@use 'base/mixins' as m;

.login {
    margin-top: 5rem;
    .uptask {
            @include m.textDegradado(v.$cyan);
        }
    }
.login-form {
    input[type="submit"] {
        border-radius: 1rem;
        @include m.boton(v.$cyan, v.$blanco);
    }
    .acciones {
        a {
            color: v.$cyan;
        }
    }
}
